import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import Typography from "@material-ui/core/Typography";
import { Badge, Divider, Paper } from "@material-ui/core";
import Theme from "../../Theme";

import { themeOverWrites } from "./Badge.stories.styles";
import { muiTheme } from "storybook-addon-material-ui";
import { jsxDecorator } from "storybook-addon-jsx";

const useStyles = makeStyles(themeOverWrites);

type BadgeStoryProps = {
    content: string | number;
    mode: string;
    className: string;
    badgeText: string;
};

const BadgeStory = ({ mode, content, badgeText }: BadgeStoryProps): React.ReactElement => {
    const classes = useStyles();

    return (
        <Paper className={classes[mode]} id="badge">
            <Typography variant="h3">Badge</Typography>
            <br />
            <Divider />
            <br />
            <div className={classes.componentWrapper}>
                <Badge classes={{ badge: classes.root }} badgeContent={content}>
                    <Typography variant="body1">{badgeText}</Typography>
                </Badge>
            </div>
        </Paper>
    );
};

const argsOptions = {
    args: {
        mode: "whiteBg",
        content: "100",
        className: "default",
        badgeText: "Texto de teste para badge"
    },
    argTypes: {
        badgeText: { type: "string" },
        content: { type: "string" },
        mode: {
            type: "select",
            options: ["whiteBg", "darkBg"]
        }
    }
};

storiesOf("Components/Badge", module)
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default", args => <BadgeStory {...args} />, argsOptions);
